
.topic{
  width: 100%;
  overflow: hidden;
  background-color: #f2f2f6;
}
.topic .topic_list{
  background-color: #fff;
  padding: 10px 10px 8px;
  margin-top: 10px;
}
.topic_list .topic_user{
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-align-items: center;
}
.topic_list .userHead{
  font-size: 0;
}
.topic_list .topic_user b{
  font-size: 10px;
  color: #94949e;
  padding: 4px 8px;
  border-radius: 15px;
  background-color: #f4f4f5;
}
.topic_list .userHead>img.userImg{
  width: 3rem;
  height: 3rem;
  border-radius: 50%;
  margin-right: .6rem;
}
.topic_list .userHead .user_msg{
  vertical-align: top;
  padding-top: .3rem;
  text-align: left;
  display: inline-block;
}
.topic_list .userHead .user_msg span {
  font-size: 1.2rem;
  color: #000;
}
.topic_list .userHead .user_msg p {
  margin-top: .6rem;
  font-size: .9rem;
  color: #94949e;
}
.topic_list .userHead .user_msg i {
  width: 15px;
  height: 25px;
  border: 1px;
  border-color: #f75894;
}
.topic_list .topic_cen{
  margin-top: 1rem;
}
.topic_list .topic_cen>p{
  font-size: 1.4rem;
  line-height: 1.9rem;
  color: #000;
}
.topic_list .topic_cen>p>b{
  width: 3.6rem;
  height: 1.3rem;
  /*padding: .1rem .2rem ;*/
  border-radius: 0.4rem;
  vertical-align: middle;
  margin-right: 0.4rem;
  margin-bottom: 0.2rem;
  border: 1px solid #ee2626;
  font-size: 1.13rem;
  padding-bottom: 0.1rem;
  color: #ee2626;
  display: inline-block;
  line-height: 1.4rem;
  text-align: center;
}
.topic_list .topic_cen_text{
  margin-top: 0.5rem;
}
.topic_list .topic_cen_text p{
  font-size: 1.2rem;
  color: #28283b;
  line-height: 1.7rem;
}
.topic_list .topic_cen_img{
  margin-top: 1.1rem;
  font-size: 0;
}
.topic_list .topic_cen_img a{
  width: 9.5rem;
  height: 9.51rem;
  display: inline-block;
}
.topic_list .topic_cen_img a:not(:last-child){
  margin-right: .8rem;
}
.topic_list .topic_cen_img a img{
  width: 100%;
  height: 100%;
}
.topic_list .topic_rem{
  margin-top: .7rem;
}
.topic_list .topic_rem ul{
  overflow: hidden;
}
.topic_list .topic_rem ul li{
  float: right;
}
.topic_list .topic_rem ul li i{
  display: inline-block;
  background-size: 100% 100%;
  background-repeat: no-repeat;
  margin-right: .6rem;
}
.topic_list .topic_rem ul li:last-child {
  margin-right: 2.2rem;
}
.topic_list .topic_rem ul li:first-child {
  margin-right: 3px;
}
.topic_list .topic_rem ul li:first-child i {
  width: 1.05rem;
  height: 1.05rem;
  background-image: url(img/drawable-hdpi/reply_1x.png);
  background-image: -webkit-image-set(url(img/drawable-hdpi/reply_1x.png)
  1x,url(img/drawable-xxhdpi/reply_2x.png)
  2x);
}
.topic_list .topic_rem ul li:last-child i {
  width: 1rem;
  height: 1rem;
  background-image: url(img/drawable-hdpi/noZan_1x.png);
  background-image: -webkit-image-set(url(img/drawable-hdpi/noZan_1x.png)
  1x,url(img/drawable-xxhdpi/noZan_2x.png)
  2x);
}
.topic_list .topic_rem ul li span{
  color: #94949e;
  font-size: 1rem;
}

/*加载更多*/
.no-list{
  height: 3rem;
  color: #95919d;
  text-align: center;
  line-height: 3rem;
  /*display: none;*/
}
/*加载中*/
.spinner {
  margin: 10px auto 10px;
  width: 60px;
  text-align: center;
  /*display: none;*/
}
.spinner > div {
  width: 15px;
  height: 15px;
  background-color: #95919d;

  border-radius: 100%;
  display: inline-block;
  -webkit-animation: bouncedelay 1.4s infinite ease-in-out;
  animation: bouncedelay 1.4s infinite ease-in-out;
  /* Prevent first frame from flickering when animation starts */
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}
.spinner .bounce1 {
  -webkit-animation-delay: -0.32s;
  animation-delay: -0.32s;
}
.spinner .bounce2 {
  -webkit-animation-delay: -0.16s;
  animation-delay: -0.16s;
}
@-webkit-keyframes bouncedelay {
0%, 80%, 100% { -webkit-transform: scale(0.0)
}
40% { -webkit-transform: scale(1.0)
}
}
@keyframes bouncedelay {
0%, 80%, 100% {
    transform: scale(0.0);
    -webkit-transform: scale(0.0);
}
40% {
      transform: scale(1.0);
      -webkit-transform: scale(1.0);
}
}



