@-webkit-keyframes bounce-in {
0% {
    -webkit-transform: translate(100%);
            transform: translate(100%);
    opacity: 0;
}
50% {
    -webkit-transform: translate(50%);
            transform: translate(50%);
    opacity: .5;
}
100% {
    -webkit-transform: translate(0);
            transform: translate(0);
    opacity: 1;
}
}
@keyframes bounce-in {
0% {
    -webkit-transform: translate(100%);
            transform: translate(100%);
    opacity: 0;
}
50% {
    -webkit-transform: translate(50%);
            transform: translate(50%);
    opacity: .5;
}
100% {
